@charset "UTF-8";

// base
html {
	font-size: 62.5%;
}
// value
$gray: #666;
$blue: #84d8d1;
$fz: (
	'sm': '1.8rem',
	'md': '3.0rem',
	'lg': '4.2rem',
) !default;

// contents
.header {
	margin: 0 auto;
	font-family: Arial,Helvetica,sans-serif;
	vertical-align: bottom;
	color: #fff;
	width: calc(100% - 1rem);
	max-width: calc(1200px - 1rem);
	padding: 1rem 0.5rem 0.5rem;
	background-color: $gray;
	&__ttl {
		font-size: 4.8rem;
	}
}
.contents {
	font-family: Arial,Helvetica,sans-serif;
	font-size: #{map-get($fz, 'sm')};
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	color: $gray;
	line-height: 1.4;
}
.box {
	overflow: hidden;
	min-height: 30rem;
}
.box01 {
	background-color: $blue;
	word-wrap: break-word;
	&__ttl {
		font-size: #{map-get($fz, 'lg')};
	}
	&__ttl--sub {
		font-size: #{map-get($fz, 'md')};
	}
}
.box02{
	position: relative;
	&__text {
		position: absolute;
		opacity: 0;
		left: 100%;
		top: 0;
		font-size: #{map-get($fz, 'lg')};
	}
}
.box03 {
	background-color: $blue;
	position: relative;
	&__text {
		position: absolute;
		opacity: 0;
		left: 0;
		top: 30%;
		font-size: #{map-get($fz, 'lg')};
	}
}
.box04 {
	background-color: $gray;
	position: relative;
	&__text{
		color: #fff;
		position: absolute;
		left: 0;
		opacity: 0;
		top: 0;
		font-size: #{map-get($fz, 'lg')};
	}
}
.footer{
	&__icon {
	text-align: center;
	position: fixed;
	z-index: 1000;
	right: 1rem;
	bottom: 3rem;
	width: 6rem;
	height: 6rem;
	border-radius: 3rem;
	background-color: rgba(238, 147, 43, .5);
	}
	&__icon--text {
	font-size: 5.4rem;
	line-height: 5.4rem;
	color: #fff;
	}
}